<template>
  <div>
    <Swipe>
      <SwipeItem v-for="item in [1,2,3]"
                 :key="item.index"><img class="swiper-img-height"
             src="@/assets/swiper.jpg"
             alt=""></SwipeItem>

    </Swipe>

    <van-cell title="官方公告"
              is-link
              value="历史公告">

      <template slot="title">
        <div class="title theme-font">官方公告</div>
      </template>
    </van-cell>
    <div class="van-list">
      <!-- <van-pull-refresh > -->
      <van-list v-model="loadingDown"
                :finished="finished"
                style="background-color:#f0f0f0">

        <news v-for="(item,index) in coupons"
              :key="index"
              :articles='item'
              ></news>
      </van-list>
      <!-- </van-pull-refresh> -->
    </div>
  </div>
</template>
<script>
import { Swipe, SwipeItem, SwipeCell, CellGroup, Cell, PullRefresh, List } from "vant";
import News from '@/components/News'

export default {
  data () {
    return {
      chosenCoupon: -1,
      coupons: [],
      // disabledCoupons: [coupon],
      showList: false,
      loadingUp: false,
      finished: false,
      loadingDown: false
    }
  },
  methods: {
    getData () {
      this.loadingDown = true
      this.$get(this.$api.getNews).then(res => {
        this.coupons = res
        this.loadingDown = false
        if (res.length < 10) {
          this.finished = true
        }
      })
    },

  },
  components: {
    Swipe,
    SwipeItem,
    // [CouponCell.name]: CouponCell,
    // [CouponList.name]: CouponList,
    [SwipeCell.name]: SwipeCell,
    [CellGroup.name]: CellGroup,
    [Cell.name]: Cell,
    [PullRefresh.name]: PullRefresh,
    [List.name]: List,
    News
  },
  created () {
    this.getData()
  }
}
</script>
