<template>
  <div>
    <van-nav-bar title="开奖公告"
                 left-arrow
                 @click-left="onClickLeft"></van-nav-bar>
    <div class="select-drop"
         @click="showPlay=!showPlay">
      {{selectObj.name}}<i class="van-icon van-icon-arrow-down"></i>
    </div>
    <div>
      <scroll :position='position'>
        <div class="item"
             v-for="(item,index) in ballList"
             :key="index">
          <div class="item-up">
            <div class="stage">{{item.expect}}期</div>
           <!-- <div class="play-result">跨河</div>
            <div class="play-result">和尾7</div>
            <div class="play-type">和尾7</div>
            -->
          </div>
          <div class="item-down">
            <div class="ball-wrap">
              <span class="ball"
                    v-for="(itemBall,index) in item.opencode"
                    :key="index">{{itemBall}}</span>

            </div>
            <span class="time">{{item.opentime|dateformat}}</span>
          </div>
        </div>
      </scroll>
    </div>
    <van-popup v-model="showPlay"
               position="bottom">
      <van-picker :columns='columns'
                  show-toolbar
                  @confirm='confirm'
                  @cancel="showPlay =false"></van-picker>
    </van-popup>
  </div>
</template>
<script>
import { Popup, Picker, Stepper, Button } from "vant";
import scroll from '@/components/ScrollWrap'
import options from '@/mixins/options'

export default {
  mixins: [options],
  data () {
    return {
      showPlay: false,
      columns: [],
      columnsData: [],
      ballList: [
        { stage: '20190516007期', balls: [1, 2, 3, 4, 5], time: 1559403069703 },
        { stage: '20190516007期', balls: [1, 2, 3, 4, 5], time: 1559403069703 },
        { stage: '20190516007期', balls: [1, 2, 3, 4, 5], time: 1559403069703 },
        { stage: '20190516007期', balls: [1, 2, 3, 4, 5], time: 1559403069703 },
        { stage: '20190516007期', balls: [1, 2, 3, 4, 5], time: 1559403069703 },
        { stage: '20190516007期', balls: [1, 2, 3, 4, 5], time: 1559403069703 },
        { stage: '20190516007期', balls: [1, 2, 3, 4, 5], time: 1559403069703 },
        { stage: '20190516007期', balls: [1, 2, 3, 4, 5], time: 1559403069703 },
        { stage: '20190516007期', balls: [1, 2, 3, 4, 5], time: 1559403069703 },
        { stage: '20190516007期', balls: [1, 2, 3, 4, 5], time: 1559403069703 },
      ],
      position: {
        top: 110,
        bottom: 51,
        left: 0,
        right: 0
      },
      selectObj:{
        name:'',
        key:''
      }
    }
  },
  components: {
    [Popup.name]: Popup,
    [Picker.name]: Picker,
    [Stepper.name]: Stepper,
    [Button.name]: Button,
    scroll
  },
  methods: {
    onRefresh (item) {
      if (item) this.selectObj = item
      this.getLottery()
      
    },
    confirm (val, index) {
      this.showPlay = false;
      console.log('确认', val, index)
      this.onRefresh(this.columnsData[index])
    },
    
    /**
     * 获取彩种
     */
    async getLotteryType () {
      await this.$get(this.$api.getLotteryType).then(res => {
        this.columnsData = res
        this.columns = this.columnsData.map(item => item.name)
        this.selectObj = this.columnsData[0]
      })
    },
    /**
     * 获取当前开奖结果
     */
    async getLottery () {
      let params = {
        code: this.selectObj.code,
        pageSize:10
      }
      await this.$get(this.$api.getLottery, { params: params }).then(res => {

        this.ballList =res.map(item=>(item.opencode=item.opencode.split(','))&& item)
      })
    },
  },
  async created () {
    await this.getLotteryType()
    this.columns = this.columnsData.map(item =>
      item.name
    )
    this.onRefresh(this.columnsData[0])
  }
}
</script>
