<template>
  <div>
    <van-nav-bar title="登录"
                 left-arrow
                 @click-left="onClickLeft"></van-nav-bar>
    <div class="acount-wrap">
      <div class="acount-center">
        <div class="acount-item">
          <van-icon name="phone-o"
                    class="acount-icon"></van-icon>
          <input type="number"
                 placeholder="请输入手机号"
                 v-model="phone"
                 class="border">
        </div>
        <div class="acount-item">
          <van-icon name="goods-collect-o"
                    class="acount-icon"></van-icon>
          <input type="password"
                 placeholder="请输入密码"
                 v-model="psw">
        </div>
        <div class="acount-tip-wrap">
          <span class="tip"
                @click="resign">还没有帐号？注册吧</span>
          <!-- <span class="tip"
                @click="forget">找回密码</span> -->
        </div>
      </div>
      <div class="acount-btn"
           @click="login">登 录</div>
    </div>

  </div>
</template>
<script>
import { NavBar, Icon, Notify } from "vant";

export default {
  data () {
    return {
      phone: '',
      psw: ''
    }
  },
  methods: {
    resign () {
      this.$router.push({ name: 'resign' })
    },
    forget () {
      this.$router.push({ name: 'forget' })

    },
    login () {
      let params = {
        psw: this.psw,
        num: this.phone
      }
      let isPhone = this.$utils.regPhoneNum(this.phone)
      if (!this.psw || !this.phone || !isPhone) {
        return this.$notify('请输入完整的账号密码')
      }
      this.$post(this.$api.login, params).then(res => {
        this.$notify({ background: '#1989fa', message: '登录成功' });
        this.$cookies.set('token', res.token);
        this.$router.push({ name: 'main' })

      }).catch(error => {
        this.$notify('账号密码有误');
      })
    },
    onClickLeft () {
      this.$router.back()
    }
  },
  created () {
  },
  components: {
    [NavBar.name]: NavBar,
    [Icon.name]: Icon,
    [Notify.name]: Notify,

  }
}
</script>
<style lang="less" scoped>
</style>
