<template>
  <div>
    <van-nav-bar :title="title"  left-arrow @click-left="$router.go(-1)">
    </van-nav-bar>
    <div v-html="editorContent">
    </div>
  </div>
</template>

<script>
  import { NavBar, Icon } from "vant";

  
  export default {
    data() {
      return {
        editorContent:'',
        title:''
      }
    },
    methods: {
      getData () {
      this.$get(this.$api.getExpert, { params: { _id: this.$route.params.id } }).then(res => {
        this.editorContent = res[0].editorContent
        this.title = res[0].title
      })
    }
    },
    components: {
      [NavBar.name]: NavBar,
      [Icon.name]: Icon,
    },
    created() {
      this.getData()
    },
  }
</script>

