<template>
  <div>
    <van-nav-bar title="专家计划" />
    <cp-notice></cp-notice>
    <van-list v-model="loading"
              :finished="finished"
              finished-text="没有更多了"
              @load='getExpert'>
      <expert-item v-for="item in objList"
                   :key="item.key"
                   :obj='item'></expert-item>
    </van-list>
  </div>
</template>
<script>
import { NavBar, PullRefresh, List } from "vant";
import ExpertItem from '@/components/ExpertItem'

export default {
  data () {
    return {
      objList: [],
      loading: true,
      finished: false
    }
  },
  methods: {

    getExpert(){
      this.$get(this.$api.getExpert).then(res=>{
        this.objList = res
        this.loading=false
        this.finished =true
      })
    }
  },
  created () {
    // this.load()
    this.getExpert()
  },
  components: {
    [PullRefresh.name]: PullRefresh,
    [List.name]: List,
    [NavBar.name]: NavBar,
    ExpertItem
  }
}
</script>
