import Router from 'vue-router'
import routers from './main/index.js'
import Vue from 'vue'
import cookies from '@/util/cookies'

Vue.use(Router)
const router = new Router({
  routes: routers
})
router.beforeEach((to, from, next) => {
  let token = cookies.get('token')

  if (to.meta.auth) {
    if (token) {
      next()
    } else {
      next({
        name: "login"
      })
    }
  } else {
    next()
  }
})


export default router