<template>
  <div>
    <van-notice-bar left-icon="volume-o">
      <slot>
        <div v-html="text"></div>
      </slot>
    </van-notice-bar>

  </div>
</template>
<script>
import { NoticeBar } from 'vant';
export default {
  data () {
    return {
      texts: [],
      index: 0
    }
  },
  components: {
    [NoticeBar.name]: NoticeBar,
  },
  props: {

  },
  methods: {
    getData () {
      this.$get(this.$api.getNotice).then(res => {
        this.texts = res.map(item => item.editorContent)
        this.timer()
      })
    },
    timer () {
      let that = this
      this.timer = setInterval(function () {
        console.log(1)
        if (that.index < that.texts.length - 1) {
          that.index++
        } else {
          that.index = 0
        }
      }, 5000)
    }

  },
  destroyed () {
    clearInterval(this.timer)
  },
  created () {
    this.getData()
  },
  computed: {
    text: function () {
      return this.texts[this.index]
    }
  }
}
</script>
