import Vue from 'vue'
import App from './App.vue'

import './assets/less/reset.less'
import './assets/less/common.less'
import router from './router'

import Notice from '@/components/Notice'
import ScrollWrap from '@/components/ScrollWrap'
import service from '@/util/axios'
import cookies from '@/util/cookies'
import api from '@/api'
import moment from 'moment'
import VueClipboard from 'vue-clipboard2'

import utils from '@/util/utils'
// import store from '@/store/index'


import ProbabilityRank from '@/components/ProbabilityRank/ProbabilityRank'

Vue.use(VueClipboard)
// Vue.use(ProbabilityRank)
Vue.prototype.$cookies = cookies
Vue.prototype.$axios = service
Vue.prototype.$post = service.post
Vue.prototype.$get = service.get
Vue.prototype.$put = service.put
Vue.prototype.$delete = service.delete
Vue.prototype.$api = api
Vue.prototype.$utils = utils

Vue.component('cp-notice', Notice)
Vue.component('scroll-wrap', ScrollWrap)
Vue.component('probability-rank', ProbabilityRank)

Vue.filter('dateformat', function (dataStr, pattern = 'YYYY-MM-DD HH:mm:ss') {
  return moment(dataStr).format(pattern)

})

Vue.config.productionTip = false


new Vue({
  router,
  // store,
  render: h => h(App),
}).$mount('#app')