<template>
  <div>
    <van-nav-bar title="倍投工具"
                 left-arrow
                 @click-left="onClickLeft"></van-nav-bar>
    <div class="multiple-wrap">
      <div class="item">
        <span>投入注数：</span>
        <input type="number"
               v-model="count">
      </div>
      <div class="item">
        <span>起始倍数：</span>
        <input type="number"
               v-model="multip">
      </div>
      <div class="item">
        <span>投入期数：</span>
        <input type="number"
               v-model="stage">
      </div>
      <div class="item">
        <span>单注奖金：</span>
        <input type="number"
               v-model="lottery">
      </div>
      <div class="item percent">
        <span>&nbsp;&nbsp;&nbsp;收益率：</span>
        <input type="number"
               v-model="percent">
      </div>
      <div class="item">
        <div class="btn"
             @click="countPersent">开始计算</div>
      </div>
    </div>
    <div class="table">
      <div class="table-head">
        <p class="index">期数</p>
        <p class="multip">投入倍数</p>
        <p class="getcrtmoney">本期投入(元)</p>
        <p class="useallmoney">累计投入(元)</p>
        <p class="getcrtmoney">本期收益(元)</p>
        <p class="getallmoney">累计收益(元)</p>
        <p class="percent">收益率</p>
      </div>
      <div v-for="(item,index) in list"
           :key="index"
           class="item">
        <p class="index">{{index+1}}</p>
        <p class="multip">{{item.multip}}</p>
        <p class="usecrtmoney">{{item.useCrtMoney}}</p>
        <p class="useallmoney">{{item.useAllMoney}}</p>
        <p class="getcrtmoney">{{item.getCrtMoney}}</p>
        <p class="getallmoney">{{item.getAllMoney}}</p>
        <p class="percent">{{item.percent}}%</p>
      </div>
    </div>

  </div>
</template>
<script>
import options from '@/mixins/options'
import { Notify } from 'vant'
export default {
  mixins: [options],
  data () {
    return {
      stage: 10, // 期数
      count: 2, // zhu数
      multip: 4, //起始倍数
      lottery: 10, // 奖金
      percent: 10,// 收益
      list: []
    }
  },
  components: {
    [Notify.name]: Notify,
  },
  methods: {
    countPersent () {
      this.list = []
      let obj = {
        stage: Number(this.stage), // 期数
        count: Number(this.count), // zhu数
        multip: Number(this.multip), //起始倍数
        lottery: Number(this.lottery), // 奖金
        percent: Number(this.percent),// 收益
        useAllMoney: 0,
        useCrtMoney: 0,
        getAllMoney: 0,
        getCrtMoney: 0
      }
      try {
        this.cycle(obj)
      } catch (error) {
        this.$notify('此方案不适合投注')
        // console.log('此方案不适合投注')
      }

    },
    cycle (obj) {
      if (!(this.list.length < this.stage)) {
        return false
      }
      let currentObj = Object.assign({}, obj)
      currentObj.getCrtMoney = Number(currentObj.multip * currentObj.lottery)
      currentObj.useCrtMoney = Number(currentObj.count * currentObj.multip * 2)
      currentObj.useAllMoney = Number(currentObj.useCrtMoney + currentObj.useAllMoney)
      currentObj.getAllMoney = Number(currentObj.getCrtMoney - currentObj.useAllMoney)

      currentObj.percent = Number(((currentObj.getAllMoney / currentObj.useAllMoney) * 100).toFixed(2))

      if (currentObj.percent >= this.percent) {
        this.list.push(currentObj);
        this.cycle(currentObj)
        console.log(currentObj)
      } else {
        let tempObj = Object.assign({}, obj)
        // let completeMuitip = this.getMultip(tempObj)
        // console.log(completeMuitip)
        // tempObj.multip += completeMuitip
        tempObj.multip += 1
        this.cycle(tempObj)
      }
    },
    getMultip (tempObj) {
      let temp = Object.assign({}, tempObj)
      console.log(temp.useAllMoney, temp.useAllMoney * (100 + this.percent), (100 * this.lottery - 200 * this.count - 2 * this.count * this.percent))
      return Math.ceil(((temp.useAllMoney * (100 + this.percent)) / (100 * this.lottery - 200 * this.count - 2 * this.count * this.percent)).toFixed(2))
    }
  },
  mixins: [options]
}
</script>
