<template>
  <div>
    <van-nav-bar title="注册账号"></van-nav-bar>
    <div class="acount-wrap">
      <div class="acount-center">
        <div class="acount-item">
          <van-icon name="phone-o"
                    class="acount-icon"></van-icon>
          <input placeholder="请输入手机号"
                 v-model="num"
                 class="border"
                 type="number">
        </div>
        <div class="acount-item">
          <van-icon name="envelop-o"
                    class="acount-icon"></van-icon>
          <div class="code_wrap">
            <input type="number"
                   placeholder="验证码"
                   v-model="code"
                   maxlength="4"
                   class="code">
            <div class="btn_code"
                 @click="getCode">{{text}}</div>
          </div>
        </div>
        <div class="acount-item">
          <van-icon name="goods-collect-o"
                    class="acount-icon"></van-icon>
          <input type="password"
                 placeholder="请输入密码"
                 v-model="psw">
        </div>
        <div class="acount-item">
          <van-icon name="goods-collect-o"
                    class="acount-icon"></van-icon>
          <input type="password"
                 placeholder="请确认密码"
                 v-model="surePsw">
        </div>
        <div class="acount-tip-wrap">
          <span class="tip"
                @click="login">已注册？去登录</span>
          <!-- <span class="tip"
                @click="forget">找回密码</span> -->
        </div>
        <div class="acount-btn"
             @click="resign">注 册</div>
      </div>
    </div>
  </div>
</template>
<script>
import { NavBar, Icon } from "vant";
import { clearInterval } from 'timers';

export default {
  data () {
    return {
      num: '',
      psw: '',
      surePsw: '',
      code: '',
      count: 180,
      timer: null
    }
  },
  methods: {
    login () {
      this.$router.push({ name: 'login' })
    },
    forget () {
      this.$router.push({ name: 'forget' })

    },
    resign () {
      if (this.psw !== this.surePsw) {
        return this.$notify('密码不一致');
      }
      let params = {

        psw: this.psw,
        num: this.num,
        code: this.code
      }
      this.$post(this.$api.register, params).then(res => {
        this.$notify({ background: '#1989fa', message: '注册成功' });

      }).catch(error => {
        this.$notify(error.message);
      })
    },
    getCode () {
      if (this.timer) return false
      let flag = this.$utils.regPhoneNum(this.num)
      if (!flag) {
        return this.$notify('请确认手机号码无误');
      }
      let params = {
        num: this.num
      }
      this.$post(this.$api.sms, params).then(res => {
        this.$notify({ background: '#1989fa', message: '短信已发送' });
        this.countTimer()
      }).catch(error => {
        this.$notify(error.message);
      })
    },
    countTimer () {

      let that = this
      this.timer = setInterval(() => {
        if (that.count > 0) {
          that.count--
        } else {
          that.count = 180
          window.clearInterval(that.timer)
          that.timer = null
        }
      }, 1000);
    }
  },
  created () {
  },
  components: {
    [NavBar.name]: NavBar,
    [Icon.name]: Icon,

  },
  computed: {
    text () {
      return this.count === 180 ? '获取验证码' : '已发送(' + this.count + 's)'
    }
  }
}
</script>
<style lang="less" scoped>
.acount-wrap .acount-center .acount-item .code_wrap {
  width: 200px;
  display: flex;
  .code {
    width: 80px;
  }
  .btn_code {
    font-size: 12.8px;
    line-height: 24px;
    color: #6892e7;
    padding: 0 10px;
  }
}
</style>
