<template>
  <div>
    <div class="multiple-wrap">
      <van-nav-bar title="过滤缩水"
                   left-arrow
                   @click-left="onClickLeft"></van-nav-bar>
      <div class="plays-wrap">
        <div class="play-btn"
             :class="selectIndex ===0?'active':''"
             @click="select(0)">二星</div>
        <div class="play-btn"
             :class="selectIndex ===1?'active':''"
             @click="select(1)">三星</div>
      </div>
      <div class="ball-box">
        <div class="box-wrap"
             v-for="(item,index) in selectAry"
             :key="index">

          <div class="word">{{index|len2name(selectAry) }}:</div>
          <div class="ball default "
               :class="item.includes(itemChild)?'active':''"
               v-for="(itemChild,indexChild) in defaultNum"
               :key="indexChild"
               @click="chooseBall(index,indexChild)">{{itemChild}}</div>
        </div>
      </div>
      <div class="btn-group">
        <!-- <van-button plain
                    type="info"
                    size="small"
                    @click="show = !show">号码选择</van-button> -->
        <van-button plain
                    type="info"
                    size="small"
                    @click="startCount">开始计算</van-button>
        <van-button plain
                    type="info"
                    size="small"
                    @click="clearAll">清除所有</van-button>
        <van-button plain
                    type="info"
                    size="small"
                    v-clipboard:copy="fliterData"
                    v-clipboard:success="onSuccess"
                    v-clipboard:error="onError">复制</van-button>
      </div>
      <scroll :position='position'>
        <div class="fliter-table-wrap">
          <div class="filter-table">
            <p class="index">序号 </p>
            <p class="num">号码</p>
            <p class="options">删除号码</p>

          </div>
          <div class="filter-table"
               v-for="(item,index) in data"
               :key="index">
            <!-- <div class="item-wrap"> -->
            <p class="index">{{index+1}} </p>
            <p class="num">{{item}}</p>
            <p class="options">
              <van-button type="default"
                          size='mini'
                          @click="deleteIndex(index)">删除</van-button>
            </p>
            <!-- </div> -->
          </div>
        </div>
      </scroll>
    </div>
    <!-- <van-popup v-model="show">
