<template>
  <div>
    <cp-notice></cp-notice>
    <div class="header-bar">

      <div class="select-item"
           @click="showPlay=!showPlay">
        {{selectObj.name}}<i class="van-icon van-icon-arrow-down"></i>
      </div>
      <div class="ball-wrap">

        <div class="ball-item">
          <span class="ball"
                v-for="(item,index) in balls"
                :key="index">{{item}}</span>

        </div>
        <div class="time">{{this.time}}</div>
      </div>
      <div class="select-item"
           @click="p_show=true"
           style="text-align: right;">
        概率排行<i class="van-icon van-icon-arrow-down"></i>
      </div>
    </div>
    <div class="lottery-type">
      <div class="lottery-item background-item "
      :class="{'lottery-select':selectPlan.code==item.code}"
           v-for="(item,index) in list"
           :key="index"

           @click="getPlanPlaysResult(item)">{{item.name}}</div>
    </div>
    <div class="page-wrap">
      <div class="page-detail background-item">
        共多少{{count}}条计划,当前{{`${Math.ceil(pageNum)+1}/${Math.ceil(count/pageSize)}`}}
      </div>
      <div class="page-btn-up lottery-item background-item" @click="prev()">上一页</div>
      <div class="page-btn-down lottery-item background-item" @click="next()">下一页</div>
    </div>
    <scroll-wrap :position="position"
                 class="border background-item">
      <van-pull-refresh v-model="isLoading"
                        @refresh="pullDown">
        <div v-for="item in listResult"
             :key='item.key' class="item-wrap">
          <div v-for="itemc in item.list"
               :key="itemc.key" class="item-fontclass">
            {{`${itemc.expect.substr(-3,3)}-${Number(itemc.expect.substr(-3,3))+(Number(itemc.maxNum)-1)}期  ${itemc.playName}${itemc.flag!=='预测中'?'【'+itemc.planNum+'】':''}${Number(itemc.currentExpect.substr(-3,3))}期 ${itemc.lotteryNum} ${itemc.flag}`}}
            <div v-if="isShowzhixuan(itemc)" v-clipboard:copy="changeLotteryType(itemc)"
                    v-clipboard:success="onSuccess"
                    class="item-fontclass"
                    >{{changeLotteryType(itemc)}}</div>
          </div>
        
        </div>

      </van-pull-refresh>
    </scroll-wrap>
    <van-popup v-model="showPlay"
               position="bottom">
      <van-picker :columns='columns'
                  show-toolbar
                  @confirm='confirm'
                  @cancel="showPlay =false"></van-picker>
    </van-popup>
    <probability-rank :show='p_show'
                      @input="dialogFlag"
                      :list='list_rank'
                      @detail='toDetail'></probability-rank>
  </div>
</template>
<script>
import { Swipe, SwipeItem, PullRefresh, List, Popup, Picker } from "vant";
let item = { name: '测试内容三' }
let l = []
for (let index = 0; index < 20; index++) {
  l.push(item)
}
export default {
  data () {
    return {
      active: 0,
      list: [],
      isLoading: false,
      showPlay: false,
      columns: [],
      columnsData: [],
      selectObj: {},
      selectPlan: {},
      position: {
        left: 2,
        right: 2,
        bottom: 51,
        top: 240
      },
      p_show: false,
      list_rank: [],
      balls: [1, 2, 3, 4, 5],
      listResult: [],
      time: 0,
      pageSize:20,
      pageNum:0,
      count:0
    }
  },
  components: {
    [PullRefresh.name]: PullRefresh,
    [List.name]: List,
    [Popup.name]: Popup,
    [Picker.name]: Picker,

  },
  methods: {
    dialogFlag (val) {
      this.p_show = val
    },
    toDetail (item) {
      this.$router.push({ name: 'lotteryHistory', params: item })
    },
    async onRefresh (item) {
      if (item) this.selectObj = item
      await this.getPlan()
    },
    async pullDown (item) {
      await this.getLottery()
      this.isLoading = false
    },
    changeWays () {
      console.log('玩法')
    },
    async confirm (val, index) {
      this.showPlay = false;
      await this.onRefresh(this.columnsData[index])
      await this.getPlanPlaysResult()
      await this.getLottery()
    },
    /**
     * 获取预测结果
     */
    getPlanPlaysResult (item) {
      if (item) {
        this.selectPlan = item
      }
      let params = {
        code: this.selectPlan.code,
        pcode: this.selectObj.code,
      }
      this.$get(this.$api.getPlanPlaysResult, { params: params }).then(res => {
        let list = res.reverse()
        this.listResult = []
        let listO = {}
        list.filter(item => {
          if (!listO[item.playCode]) {
            listO[item.playCode] = []
          }
          listO[item.playCode].push(item)
        })
        for (const key in listO) {
          if (listO.hasOwnProperty(key)) {
            const element = listO[key];
            element.sort(function (val, val1) {
              return val.createTime - val1.createTime
            })
            this.listResult.push({ list: element })
          }
        }
        let rangeList =[]
        this.listResult.map(item=>{
          let obj ={}
          obj.stage =item.list.length
          let count = 0
          
          item.list.map(item=>{
            if(!obj.title)obj.title=item.playName
            if(item.flag === '中') count++
          })
          obj.p=Math.floor(count/obj.stage*100)
          rangeList.push(obj)
        })
        
        rangeList.sort((a,b)=>{
           return b.p-a.p
        })
        this.list_rank=rangeList
      })
    },
    /**
     * 获取当前开奖结果
     */
    async getLottery () {
      let params = {
        code: this.selectObj.code,
      }
      await this.$get(this.$api.getLottery, { params: params }).then(res => {
        let item = res[0]
        this.balls = item.opencode.split(',')
        this.time = item.opentime.substr(-8, 5)
      })
    },
    /**
     * 获取彩种
     */
    async getLotteryType () {
      await this.$get(this.$api.getLotteryType).then(res => {
        this.columnsData = res
        this.columns = this.columnsData.map(item => item.name)
        this.selectObj = this.columnsData[0]
      })
    },
    /**
     * 获取彩种下的计划
     */
    async getPlan () {
      await this.$get(this.$api.getPlan, {
        params: {
          pcode: this.selectObj.code,
          pageSize:this.pageSize,
          pageNum:this.pageNum
        }
      }).then(res => {
        this.list = res.list
        this.count=res.count
        this.selectPlan = this.list[0]
      })
    },
    async initData () {
      this.listResult = []
    },
    changeLotteryType(item){
      let str = item.planNum
      if(str.indexOf('*')<0 || item.flag!== '预测中'){
        return ''
      }
     let list = str.split('*')
     return  this.forList(list).join(' ')
    },
    forList(list){
      let resultAry=[]
      for(let idx = 0; idx<list.length;idx++){
        // let nextIdx=idx+1
        if(list[idx]){
          let tmpList = list[idx].split('')
          resultAry = this.temp(resultAry,tmpList)
        }
      }
      return resultAry
    },
    temp(tmpAry,list){
      let ary = []
      if(tmpAry.length ===0){
          list.forEach(ic =>{
            ary.push(ic)
          })

      }else{
        tmpAry.forEach(item=>{
              list.forEach(ic =>{
                ary.push(item+ic)
            })
        })
      }
      return  ary
    },
    isShowzhixuan(item){
      if((item.playCode.indexOf('zhix')>-1)&&item.planNum &&(item.flag === '预测中')){
        return true
      }else{
        return false
      }
    },
    onSuccess(){
        this.$notify({ background: '#1989fa', message: '复制成功' });
    },
    async prev(){
      if(this.pageNum>0){
        this.pageNum--
        await this.getPlan()
          await this.getPlanPlaysResult()
        }
    },
  async next(){
      if(this.pageNum<Math.floor(this.count/this.pageSize)){
        this.pageNum++
        await this.getPlan()
        await this.getPlanPlaysResult()
      }
    }
  },
  async created () {
    await this.getLotteryType()
    await this.onRefresh(this.selectObj)
    await this.getPlanPlaysResult()
    await this.getLottery()
  },
  computed: {
    expectF:function(val){
      if(val.length==3){
return '0'+val
      }
    }
  },
}
</script>
