<template>
  <div>
    <van-nav-bar title="修改密码"></van-nav-bar>
    <div class="acount-wrap">
      <div class="acount-center">
        <div class="acount-item">
          <van-icon name="phone-o"
                    class="acount-icon"></van-icon>
          <input type="password"
                 placeholder="输入旧密码"
                 v-model="oldpsw"
                 class="border">
        </div>
        <div class="acount-item">
          <van-icon name="goods-collect-o"
                    class="acount-icon"></van-icon>
          <input type="password"
                 placeholder="请输入新密码"
                 v-model="psw">
        </div>
        <div class="acount-item">
          <van-icon name="goods-collect-o"
                    class="acount-icon"></van-icon>
          <input type="password"
                 placeholder="请确认新密码"
                 v-model="surePsw">
        </div>
        <div class="acount-tip-wrap">
          <span class="tip"
                @click="resign">还没有帐号？注册吧</span>
          <span class="tip"
                @click="login">已有账号,去登陆</span>
        </div>
        <div class="acount-btn"
             @click="changePsd">确认修改</div>
      </div>
    </div>
  </div>
</template>
<script>
import { NavBar, Icon } from "vant";

export default {
  data () {
    return {
      phone: '',
      psw: ''
    }
  },
  methods: {
    resign () {
      this.$router.push({ name: 'resign' })
    },
    login () {
      this.$router.push({ name: 'login' })

    },
    changePsd () {
      let params = {
        password: this.psw,
        userName: this.phone
      }
      this.$post(this.$api.login, {
        params: params
      }).then(res => {
        this.$notify({ background: '#1989fa', message: '登录成功' });
      }).catch(error => {
        this.$notify(error.message);
      })
    }
  },
  created () {
  },
  components: {
    [NavBar.name]: NavBar,
    [Icon.name]: Icon,

  }
}
</script>
