<template>
  <div>
    <van-nav-bar :title="title"
                 left-arrow
                 @click-left="onClickLeft"></van-nav-bar>
    <div v-html="editorContent"></div>
  </div>
</template>
<script>
import { NavBar, Icon } from "vant";
import options from '@/mixins/options.js'
export default {
  mixins: [options],
  data () {
    return {
      title: '',
      editorContent: ''
    }
  },
  methods: {
    getData () {
      console.log(this.$route)
      this.$get(this.$api.getNews, { params: { _id: this.$route.params.id } }).then(res => {
        this.editorContent = res[0].editorContent
        this.title = res[0].title
      })
    }
  },
  created () {
    this.getData()
  },
  components: {
    [NavBar.name]: NavBar,
    [Icon.name]: Icon,

  }
}
</script>
<style lang="less" scoped>
.title {
  text-align: center;
  font-size: 16px;

  color: #333333;
  padding: 20px 0;
}
.word-wrap {
  font-size: 11px;

  line-height: 18px;

