<template>
  <div>
    <van-nav-bar title="开奖历史"
                 left-arrow
                 @click-left="onClickLeft"></van-nav-bar>
    <div class="item">
      <div>第{{stage}}期</div>
      <div>截止开奖：<span class="time">{{time}}</span></div>
    </div>
    <div class="item">
      <div>开奖号码：</div>
      <div> <span class="ball"
              style="margin: 0 2px 0"
              v-for="(item,index) in balls"
              :key="index">{{item}}</span>
      </div>
    </div>
    <scroll :position='position'
            style="background-color:#fff">
      <div class="padding">12321</div>
    </scroll>
  </div>
</template>
<script>
import options from '@/mixins/options'
import scroll from '@/components/ScrollWrap'
export default {
  mixins: [options],
  data () {
    return {
      stage: 20190101,
      time: '09：09',
      balls: [0, 1, 2, 3, 4],
      position: {
        top: 130,
        left: 0,
        right: 0,
        bottom: 55
      }
    }
  },
  components: {
    scroll
  },
  methods: {
    /**
    * 获取当前开奖结果
    */
    async getLottery () {
      let params = {
        code: this.selectObj.code,
      }
      await this.$get(this.$api.getLottery, { params: params }).then(res => {
        let item = res[0]
        this.balls = item.opencode.split(',')
        this.time = item.opentime.substr(-5, 5)
      })
    },
  },
}
</script>
